// 多语言组件 by shy07

import React, { useContext } from 'react'

import { getUserLocales } from './getUserLocale'
import cnZHS from './cnZHS.json'
import enUS from './enUS.json'

// 多语言字典，import 以后，添加到这里即可
const languageDictionaries = {
  cnZHS,
  enUS
}

const userLocale = () => {
  const uls = getUserLocales()
  if (uls.includes('zh') || uls.includes('cn')) return 'cnZHS'
  else return 'enUS'
}

const localLanguage = (() => {
  try {
    const ll = window.localStorage.getItem('_site_lang_')
    if (ll === 'zhCNS') {
      window.localStorage.setItem('_site_lang_', 'cnZHS')
      return 'cnZHS'
    }
    if (!ll) {
      return userLocale()
    }
    return ll
  } catch (err) { return 'enUS' }
})()

export const languages = Object.keys(languageDictionaries)

// 切换语言并保存
export const lang = t => key => {
  try {
    window.localStorage.setItem('_site_lang_', t)
  } catch (err) {}
  return (languageDictionaries[t] || languageDictionaries.cnZHS)[key] || key
}

export const defaultLanguage = lang(localLanguage)

export const LanguageContext = React.createContext(defaultLanguage)
export const LanguageProvider = LanguageContext.Provider
export const LanguageConsumer = LanguageContext.Consumer

// 绑定语言环境，只有使用此函数包装过的组件才能随设置切换语言
// 被包装的函数中需要用 this.props._l 函数处理文字
export const bindLanguage = C => props => {
  const { language, changeLanguage } = useContext(LanguageContext)
  return <C {...props} _l={language} _cl={changeLanguage} />
}

export const T = props => {
  const { language } = useContext(LanguageContext)
  const { children } = props
  return language(children)
}

export const useLanguage = () => {
  const { language } = useContext(LanguageContext)
  return { language, _l: language }
}
