import React, { useContext, useState } from 'react'

import AppIcon from './AppIcon'
import { languages, lang, LanguageContext } from './language'

const APP_ID = 'cac576c6'

const Language = props => {
  const { language, changeLanguage } = useContext(LanguageContext)
  const [closeFlag, setCloseFlag] = useState(false)
  const primaryColor = '#F7B5B4'

  const afterClose = () => setCloseFlag(false)

  const handleChange = val => () => {
    changeLanguage(lang(val))
    setCloseFlag(true)
  }

  const list = languages.map((val, idx) => (
    <span key={idx} onClick={handleChange(val)}>{language(val)}</span>
  ))

  return (
    <AppIcon
      primaryColor={primaryColor}
      icon={<span style={{ fontSize: '2.4vh' }}>Abc|文</span>}
      _appID={APP_ID}
      name='Language'
      title={
        <span>Language</span>
      }
      closeFlag={closeFlag}
      afterClose={afterClose}
    >
      <div className='lang-app-container'>
        {list}
      </div>
    </AppIcon>
  )
}

export default Language
