import React, { useState, useEffect } from 'react'

import { useLanguage } from './language'
import { useGlobal } from './global'

const AppIcon = props => {
  const { _g } = useGlobal()
  const { appId } = _g
  const {
    primaryColor, name, icon, children, title, debug, _appID, closeFlag,
    afterClose, loading
  } = props
  const openness = _appID === appId ? true : debug || false
  const [open, setOpen] = useState(openness)
  const [iconVisible, setIconVisible] = useState(!openness)
  const [containerVisible, setContainerVisible] = useState(openness)
  const { _l } = useLanguage()

  useEffect(() => {
    if (closeFlag) {
      handleCloseApp()
      afterClose && afterClose()
    }
  }, [closeFlag])

  const handleOpenApp = () => {
    if (!open) {
      setIconVisible(false)
      setOpen(true)
      setTimeout(() => { setContainerVisible(true) }, 400)
    }
  }

  const handleCloseApp = () => {
    setContainerVisible(false)
    setOpen(false)
    setTimeout(() => { setIconVisible(true) }, 400)
  }

  return (
    <div className='app-icon'>
      <div
        className={`bottom ${open ? 'open' : ''}`}
        style={{ background: primaryColor }}
        onClick={handleOpenApp}
      >
        {iconVisible && <div className='icon'>{icon}</div>}
      </div>
      <div className='name'>{name || 'Unknown'}</div>
      {containerVisible && (
        <div className='app-container'>
          <div className='app-header'>
            <div className='close' onClick={handleCloseApp}>{_l('CLOSE')}</div>
            <div className='title'>{title}</div>
            <div className='loading'>
              <div className='icon'>
                {loading && <div className='sbl-circ-path' />}
              </div>
            </div>
          </div>
          <div className='app-content'>
            {children}
          </div>
        </div>
      )}
    </div>
  )
}

export default AppIcon
