import React, { useState } from 'react'
import dynamic from 'next/dynamic'

import AppIcon from '../AppIcon'
import { useLanguage } from '../language'

const APP_ID = 'd49db8e0'
const App = dynamic(() => import('./app'), { ssr: false })

const WIS = props => {
  const primaryColor = '#FFB021'
  const { _l } = useLanguage()
  const [loading, setLoading] = useState(false)

  return (
    <AppIcon
      primaryColor={primaryColor}
      icon={<span style={{ fontSize: '4.6vh' }}>GAM</span>}
      _appID={APP_ID}
      name={_l('GenuineArtMuseum')}
      title={
        <span>{_l('GAM - Genuine Art Museum')}</span>
      }
      loading={loading}
    >
      <App _appID={APP_ID} setLoading={setLoading} />
    </AppIcon>
  )
}

export default WIS
