import React from 'react'

import '../less/style.less'

const Desktop = props => {
  const { wallpaper, children } = props
  const defaultColor = '#ECDBB1'
  const background = wallpaper ? `url(${wallpaper})` : defaultColor

  return (
    <div className='desktop' style={{ background }}>
      <div className='inner'>
        {children}
      </div>
    </div>
  )
}

export default Desktop
